











































import { Vue, Component } from 'vue-property-decorator';
import Loading from '@/../components/loading/loading.vue';

@Component({
    components: {},
})
export default class VuePageClass extends Vue {
    private loading = new Loading();

    private delaySeconds: number = 5;

    private get title(): string {
        return this.$i18n.Component_Loading_Title.replace(/\{\{delaySeconds\}\}/gm, this.delaySeconds.toString());
    }

    private clickShowLoading(): void {
        this.loading.show();
        setTimeout(() => this.loading.hide(), this.delaySeconds * 1000);
    }
}
